import React, { Component } from 'react';
import '../styles/showcaseItem.css';
import {Card} from 'material-ui/Card';

const images = {
  avatar: require('../assets/avatar/cl-min.jpg'),
};

const styles = {
  avatar: {
    width: 120,
    height: 120,
    borderRadius: 60,
    maxWidth: 120,
  },
  content: {
    paddingTop: 12,
    paddingBottom: 12,
    paddingLeft: 12,
  },
};

class About extends Component {
  state = {
  };

  render() {
    return (
      <div className="col-sm-8 offset-sm-2">
        <Card
        >
          <div className="row" style={styles.content}>
            <div className="col-sm-2">
              <img className="app-icon-shadow" src={images.avatar} alt="" style={styles.avatar} />
            </div>

            <div className="col-sm-10" style={{margin: 'auto'}}>
              <div>
                Hey I am Chin Loong.
              </div>
              <div>
                I develop cross platform apps using React Native.
              </div>
              <div>
              </div>
              <div>
              </div>
            </div>
          </div>
        </Card>
      </div>
    );
  }
}

export default About;



