import React, { Component } from 'react';
import '../styles/footer.css';
import FontAwesomeIcon from '@fortawesome/react-fontawesome'
import faLinkedIn from '@fortawesome/fontawesome-free-brands/faLinkedin'
// import faFacebook from '@fortawesome/fontawesome-free-brands/faFacebook'
import GA from '../lib/utils/GA';

const brandColors = {
  linkedIn: '#0077B5',
  facebook: '#3b5998',
};

const myEmail = 'chinloong.code@gmail.com';
const myLinkedIn = 'https://www.linkedin.com/in/chinloongtan';

class Footer extends Component {
  state = {
    isLinkedInActive: false,
    isFacebookActive: false,
  };

  render() {
    return (
      <footer className="">
        <div className="container">
          <div className="">
            I am reachable via LinkedIn message.
          </div>

          <a
            href={myLinkedIn}
            target="_blank"
            rel="noopener noreferrer"
            onClick={() => GA.trackLinkEvent(myLinkedIn)}
          >
            <FontAwesomeIcon
              icon={faLinkedIn}
              size="4x"
              // onMouseEnter={() => this.setState({ isLinkedInActive: true })}
              // onMouseLeave={() => this.setState({ isLinkedInActive: false })}
              // color={this.state.isLinkedInActive ? brandColors.linkedIn : null}
              color={brandColors.linkedIn}
              style={{marginRight: 10, marginTop: 15, marginBottom: 15,}}
            />
          </a>

      {/*
          <a href="https://www.facebook.com/chinloong91" target="_blank">
            <FontAwesomeIcon
              icon={faFacebook}
              size="4x"
              // onMouseEnter={() => this.setState({ isFacebookActive: true })}
              // onMouseLeave={() => this.setState({ isFacebookActive: false })}
              // color={this.state.isFacebookActive ? brandColors.facebook : null}
              color={brandColors.facebook}
              style={{marginRight: 10, marginTop: 15, marginBottom: 15,}}
            />
          </a>
        */}

          <div className="" style={{marginBottom: 0}}>
            <a href="mailto:chinloong.code@gmail.com" onClick={() => GA.trackLinkEvent('contactEmail')}>
              {myEmail}
            </a>
          </div>

          <div className="" style={{marginTop: 20}}>
            Website by Chin Loong Tan ©2018
          </div>
        </div>

      </footer>
    );
  }
}

export default Footer;



