import React, { Component } from 'react';
import './App.css';
import Showcase from './showcase';
import Footer from './footer';
import About from './about';
import Recommendation from './recommendation';
import Navbar from './navbar';
import scrollToComponent from 'react-scroll-to-component';

class App extends Component {
  constructor(props) {
    super(props);

    this.navbarOffsetFromTop = null;
    this.state = {
      fixNav: false,
    };
  }

  onAppScroll = (event) => {
    if (this.navbarOffsetFromTop === null) return;

    const scrollTop = document.documentElement.scrollTop;
    if (scrollTop >= this.navbarOffsetFromTop) {
      this.setState({ fixNav: true })
    } else {
      this.setState({ fixNav: false })
    }
  }

  onAppMount = (node) => {
    if (!node) return;

    window.addEventListener('scroll', this.onAppScroll, true);
  }

  onNavMount = (node) => {
    if (!node) return;

    const position = node.getBoundingClientRect();
    this.navbarOffsetFromTop = position.top;
  }

  render() {
    return (
      <div
        ref={this.onAppMount}
        id="app"
        className={this.state.fixNav ? 'fix-navbar' : ''}
      >
        <div className="name-container" style={{height: 250, backgroundColor: '#e9ecef', marginBottom: 40, position: 'relative'}}>
          <div className="container">
            <h1 className="display-4">Chin Loong Tan</h1>
            <p className="lead">React Native Developer</p>
          </div>

          <div className="custom-nav"
            ref={this.onNavMount}
          >
            <Navbar
              scrollTo={(page = 'about') => {
                const scrollRefs = {
                  about: this.aboutPage,
                  work: this.workPage,
                  contact: this.contactPage,
                }

                scrollToComponent(
                  scrollRefs[page],
                  { offset: -100, align: 'top', duration: 500 }
                );
              }}
            />
          </div>
        </div>

        <About
          ref={elem => {
            this.aboutPage = elem;
          }}
        />

        <hr />

        <Showcase
          ref={elem => {
            this.workPage = elem;
          }}
        />

        <Recommendation />

        <Footer
          ref={elem => {
            this.contactPage = elem;
          }}
        />
      </div>
    );
  }
}

export default App;



